/* src/App.css */
.App {
  text-align: center;
}

.my-component {
  font-family: Arial, sans-serif;
  margin: 20px;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.my-component h1 {
  color: #333;
}

.my-component p {
  color: #666;
}